.header { 
  background-size: cover;
  background-position: center;
  background-image: url(../images/header.jpg);

  &__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  box-sizing: border-box;
  height: 100vh;
  padding: $headerTopPadding 0 40px;
  }

  &__title {
    font-weight: bold;
    font-size: 46px;
    line-height: 40px;
    color: #fff;
    letter-spacing: -1px;

    @include onDesctop {
      font-size: 80px;
      line-height: 80px;
      letter-spacing: -1.5px;
    }

    &::before {
      content: "";
      display: block;
      width: 40px;
      height: 2px;
      background-color: #fff;
      margin-bottom: 24px;
    }
  }

  &__call {
    text-decoration: none;

    @include onDesctop {
      &::before {
        content: '+1 234 555-55-55';
        position: relative;
        top: 50%;
        transform: translate(-100%, -50%);
        
        display: block;
        width: max-content;
        padding-right: 20px;
        
        font-weight: bold;
        font-size: 12px;
        line-height: 16px;
        color: #fff;
        letter-spacing: 2px;
        text-transform: uppercase;
        opacity: 0;
        pointer-events: none;
        transition: 0.3s;
      }

      &:hover::before {
        pointer-events: all;
        opacity: 1;
      }

    }
  }
}
